import axios from "axios";
import { store } from "../store";
import { logout } from "../features/authSlice"; //    
import { ErrorMessages } from "../constants/Message";
import { ROUTES } from "../constants/routes";

let alertHandler = null;
let setLoading = null;

//   alert  
export const setAlertHandler = (handler) => {
  alertHandler = handler;
};

//   loading  
export const setLoadingHandler = (handler) => {
  setLoading = handler;
};

const instance = axios.create({
  baseURL: "/api",
  timeout: 10000,
});

//  
instance.interceptors.request.use((config) => {
  const token = localStorage.getItem("accessToken");
  if (token) {
    config.headers.Authorization = `Bearer ${token}`;
  }

  setLoading?.(true);
  return config;
});

//  
instance.interceptors.response.use(
  (res) => {
    setLoading?.(false);
    return res;
  },
  (error) => {
    setLoading?.(false);

    const status = error.response?.status;
    const message = error.response?.data?.message || error.message;

    if (alertHandler) {
      switch (status) {
        case 401:
          localStorage.removeItem("accessToken");
          alertHandler({
            message: ErrorMessages.expired,
            onConfirm: () => {
              store.dispatch(logout());
            },
          });
          break;

        case 403:
          alertHandler({ message: ErrorMessages.noPermission });
          break;

        case 404:
          alertHandler({ message: ErrorMessages.noObject });
          break;

        case 500:
          alertHandler({ message: ErrorMessages.server });
          break;

        default:
          alertHandler({ message });
      }
    }

    return Promise.reject(error);
  }
);

export default instance;
